<template lang="html">
  <section class="quote-list">
    <div class="quote" v-for="(quote, i) in quotes.slice().reverse()" :Key="i">
      <div class="quote-content">
        {{ quote.content }}
      </div>
    </div>
  </section>
</template>

<script>
export default {
  props: ["quotes"],
};
</script>

<style lang="scss" scoped>
.quote-list {
  padding: 32px;
  max-width: 720px;
  margin: 0 auto;

  .quote {
    background-color: var(--Light);
    border-radius: 16px;
    padding: 16px;
    margin-bottom: 16px;

    .quote-content {
      line-height: 18px;
      max-height: 18px;
      overflow: hidden;
      text-overflow: ellipsis;
      &:hover {
        max-height: unset;
        white-space: unset;
      }
    }
  }
}
</style>
