<template>
  <div class="app">
    <Header title="The Anime Quoter" />
    <Quote :quote="quote" />
    <div class="button-container">
      <button @click="getQuotes">Generate</button>
    </div>
    <QuteList :quotes='quotes' />
  </div>
</template>

<script type="text/javascript">
import Header from "./components/Header.vue";
import Quote from "./components/Quote.vue";
import QuteList from "./components/QuteList.vue"
export default {
  name: "App",
  components: {
    Header,
    Quote,
    QuteList

  },
  data() {
    return {
      quote: {},
      quotes:[]
    };
  },
  methods: {
    async getQuotes() {
      if(this.quote.content)
      {
        console.log(this.quotes = [...this.quotes, this.quote])
      }

      const data = await fetch("https://animechan.vercel.app/api/random").then(
        (response) => response.json()
      );

    console.log( this.quote = {
        content: data.quote,
        anime: data.anime,
        character: data.character,
      });
    },
  },
  created() {
    this.getQuotes();
  },
};
</script>

